import React from 'react';
import { useEffect } from 'react' 
import cl from './SettingsModal.module.scss'

const SettingsModal = ({visible, postId, setVisible, remove, ...props}) => {
	const modalClasses = [cl.modal]
	if (visible){
		modalClasses.push(cl.active)
	}
	return (
		<div  className={modalClasses.join(' ')} onClick={()=>setVisible(false)} onMouseLeave={(e) => {setVisible(false)}}>
			<div className={cl.content}>
				<button onClick={()=>{remove(postId)}}>Удалить запись</button>
			</div>
		</div>
	);
};

export default SettingsModal;