import React from 'react';
// import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { useState, useEffect } from 'react' 
import axios from 'axios'
import UpTools from '../UpTools/UpTools';
import DownTools from '../DownTools/DownTools';
import Comments from '../comments/Comments';

const Post = ({users, postId, post, setModal, photos, remove, setPhotoStep, setPhotosArray}) => {
	

	const [likesClicked, setLikesClicked] = useState(false);
	const [commentsVisibility, setCommentsVisibility] = useState(false)
	const [commentsLength, setCommentsLength] = useState(0)
	const gridOptions = ''
	
	let userName = users[post.userId-1].firstName +' '+ users[post.userId-1].secondName
	return (
		<div className='body'>
			<UpTools post={post} remove={remove} postId={postId} userName = {userName} userId = {post.userId} className='UpTools'></UpTools>
			<div className='photos'>
					{photos.map((photo,step)=>
						<div key = {photo} className='photo__container'>
							<img  src={photo} onClick={e=>{setModal(true); setPhotoStep(step); console.log(step, photos); setPhotosArray(photos)}}></img>
						</div>
					)}

			</div>
			<div className='description'>{post.text}</div>
			<DownTools commentsLength={commentsLength}
				commentsVisibility={commentsVisibility} setCommentsVisibility={setCommentsVisibility} 
				className='downTools' likes={post.likes} dislikes={post.dislikes}>
			</DownTools>
			<Comments users={users} setCommentsLength={setCommentsLength} postId={postId} commentsVisibility={commentsVisibility}></Comments>
			
		</div>
	);
};

export default Post;