import React from 'react';
import { useState, useEffect, useRef } from 'react' 
import cl from './CreatePost.module.scss'
import Emoji from './Emoji/Emoji';
import EmojiPicker from "rm-emoji-picker";
// import { findDOMNode } from "react-dom";
import Picker, { SKIN_TONE_MEDIUM_DARK } from 'emoji-picker-react';
import $ from "jquery";
// import { elementAttributeModified } from 'jsdom/lib/jsdom/living/named-properties-window';

const CreatePost = ({posts, setPosts, totalCount, setTotalCount, ...props}) => {

	// const [biggerVisibile, setBiggerVisible] = useState(false);
	// const [inpJustClicked, setInpJustClicked] = useState(false);
	const [photos, setPhotos] = useState([])
	const [value, setValue] = useState('');
	const [active, setactive] = useState(false)
	// const textarea = useRef(null);
	// useEffect(() => {
	// 	if (biggerVisibile){
	// 		setInpJustClicked(true)
	// 		const onClick = e => textarea.current.contains(e.target) || inpJustClicked || setBiggerVisible(false);
	// 		setInpJustClicked(false);
	// 		textarea.focus();
	// 		document.addEventListener('click', onClick);
	// 		return () => document.removeEventListener('click', onClick);
	// 	}
	// }, [biggerVisibile, setBiggerVisible, setInpJustClicked, inpJustClicked]);


	// const body = biggerVisibile
	// ?<textarea value={value} onChange={(e)=>setValue(e.target.value)}
	// ref={textarea} className={cl.text} placeholder='Новый ТА'></textarea>

	// :<input className={cl.input} placeholder='Новый инпут' value={value}
	// onClick={()=>{setBiggerVisible(true); setInpJustClicked(true)}}
	// onChange={(e)=>setValue(e.target.value)}
	// ></input>

	// {body}

	const createPost = (value) =>{
		let date = Date.now()
		let d = new Date()
		console.log(d.getMonth(date))
		date = d.getFullYear(date)+'-'+(d.getMonth(date)+1)+'-'+d.getDate(date)+'T'+d.getHours(date)+':'
		+d.getMinutes(date)+':'+d.getSeconds(date)+'Z'
		let obj= {createdAt: date,
			dislikes: Math.floor(Math.random())*20,
			id: totalCount+1	,
			likes:  Math.floor(Math.random())*20,
			text: value,
			photos: photos,
			userId: 1,
		}
		setPosts([obj, ...posts])
		setTotalCount(prev=>prev+1)
		setValue('')
	}
	function fileChange(){

		let phArr = []
		var file = document.getElementById('input_img');
		console.log(file.files.length)
		for (let el in file.files){
			var form = new FormData();
		  if (el!=='length'&&el!=='item'){
			form.append("image", file.files[el])
			var settings = {
			"url": "https://api.imgbb.com/1/upload?key=ba4ca409ca2b7f81870f232a23f73b79",
			"method": "POST",
			"timeout": 0,
			"processData": false,
			"mimeType": "multipart/form-data",
			"contentType": false,
			"data": form
			};
			
			
			$.ajax(settings).done(function (response) {
			//   console.log(response);
			var jx = JSON.parse(response);
			phArr.push(jx.data.url)
			console.log(jx.data)
			// setPhotos([...photos, jx.data.url]);
			})
		  }
		}
		setPhotos(phArr)
	}
	// const picker = new EmojiPicker();
	// const icon      = document.getElementById('emoji');
	// const container = document.getElementById('container');
	// const editable  = document.getElementById('my-input');

	// picker.listenOn(icon, container, editable);
	const [chosenEmoji, setChosenEmoji] = useState(null);
	const [emPickerVisib,setEmPickerVisib] = useState(false)
	const onEmojiClick = (event, emojiObject) => {
	  setChosenEmoji(emojiObject);
	  setValue(prev=>prev+emojiObject.emoji)
	};
	return (
		<div className={`${cl.body} ${cl.active}`}>
			
			<img className={cl.image} src={require(`../UpTools/avatars/1.png`)} alt='ава'></img>
			<input  className={cl.file} type="file" multiple id="input_img" onChange={()=>{fileChange()}} accept="image/*"></input>
			<label htmlFor="input_img" className={cl.input__file_button}>
				<img className={cl.input__file_icon} src={require("./attach.png")} alt="Выбрать файл" width="25"></img>
			</label>
			<textarea value={value}  className={cl.text} onChange={(e)=>setValue(e.target.value)}></textarea>

			<button onClick={()=>setEmPickerVisib(prev=>!prev)}><Emoji symbol="🐑"  label="sheep"/></button>
			{emPickerVisib?
			<div>
				<div onMouseLeave={()=>setEmPickerVisib(false)} style={{zIndex:'13', position:'absolute'}}>
				<Picker
					onEmojiClick={onEmojiClick}
					disableAutoFocus={true}
					skinTone={SKIN_TONE_MEDIUM_DARK}
					groupNames={{ smileys_people: 'PEOPLE' }}
					native
				/>
				</div>
			</div>
			:''}
			<button className={cl.btn} onClick={()=>{setTimeout(createPost(value), 3000); setPhotos([])}}>Создать пост</button>
		</div>
	);
};

export default CreatePost;