import './App.scss';
import React from 'react'
import { useState, useEffect } from 'react' 
import axios from 'axios'
import Post from './components/Post/Post'
import PhotoModal from './components/PhotoModal/PhotoModal';
import {TransitionGroup, CSSTransition} from 'react-transition-group'
import CreatePost from './components/CreatePost/CreatePost';
import Comments from './components/comments/Comments';
// import fetchToCurl from 'fetch-to-curl';


function App() {
	// const [photos, setPhotos]= useState([]) 
	const [currentPage, setCurrentPage] = useState(1)
	const [fetching, setFetching] = useState(true)
	const [totalCount, setTotalCount] = useState(100)
	const [users, setUsers] = useState({})
	const [posts, setPosts] = useState([])
	const [data, setData] = useState([])
	
	const removePost = (id) => {
		console.log('going')
		console.log(id)
		setPosts(posts.filter(p=>p.id!==id))
	};


	useEffect(() => {
		if (fetching){
			axios.get(`https://raw.githubusercontent.com/checkforants/ics/main/PostsDB.json`)
				.then(response => {
					setData(response.data)
					setPosts([...posts, ...response.data.slice((currentPage-1)*10,currentPage*10)])
					setCurrentPage(prevState => prevState+1)
				}).finally(()=> setFetching(false))
		}
	}, [fetching]);




	const getUsers = () => {
		axios.get(`https://raw.githubusercontent.com/checkforants/ics/main/usersDB.json`).then(response =>
			{
				setUsers(response.data)
			})
	}

	useEffect(() => {
		getUsers()
		document.addEventListener('scroll', scrollHandler)
		return function(){
			document.removeEventListener('scroll', scrollHandler)
		}
	}, []);

	const scrollHandler = (e) => {
		if (e.target.documentElement.scrollHeight - (e.target.documentElement.scrollTop 
		 + window.innerHeight) <100 && posts.length<totalCount){
				setFetching(true)
			}
	}
	
	const[modal, setModal] = useState(false)
	const[photoStep, setPhotoStep] = useState(0)
	const[photosArray, setPhotosArray] = useState([])
	


  return (
    <div className="App">
		<CreatePost posts={posts} setPosts={setPosts} totalCount={totalCount} setTotalCount={setTotalCount}> </CreatePost>
		<PhotoModal visible={modal} step={photoStep} setVisible={setModal} photos={photosArray} photoStep={photoStep} setPhotoStep={setPhotoStep}></PhotoModal>
		<TransitionGroup>
		{posts.map((post, step) =>
			<CSSTransition
				key={post.id}
				timeout={550}
				classNames='post'>
			<Post users={users}
			 remove={removePost} photos={post.photos} postId={post.id} post={post} setModal={setModal} setPhotoStep={setPhotoStep} setPhotosArray={setPhotosArray}></Post>
			</CSSTransition>
			
			)
		}
		</TransitionGroup>
    </div>
  );
}

export default App;
