import React from 'react';
import cl from './Uptools.module.scss'
import { useState, useRef, useEffect } from 'react';
import SettingsModal from '../SettingsModal/SettingsModal';
const UpTools = ({userName, userId, remove, photoId, postId, post, ...props}) => {

	const [settingsModal, setSettingsModal] = useState(false);
	
	const rootEl = useRef(null);
	useEffect(() => {
			const onClick = e => rootEl.current.contains(e.target) || setSettingsModal(false);
			document.addEventListener('click', onClick);
			return () => document.removeEventListener('click', onClick);

	}, [setSettingsModal]);

	const month = 	['Янв',	'Февр','Мар','Апр','Мая','Июня','Июля','Авг','Сент','Окт','Нояб','Дек']
	let date = post.createdAt.split('T')[0].split('-')
	let monthNum = date[1][0]!==0?date[1]:date[1][0]
	date = date[2] +' ' + month[monthNum-1]+' '+date[0]
	return (
		<div className={cl.uptools}>
			<SettingsModal visible={settingsModal} setVisible={setSettingsModal} remove={remove} postId={postId}></SettingsModal>
			<div className={cl['left-part']} >
				<div className={cl.avatar}>
					<img src={require(`./avatars/${userId}.png`)} alt='ава' ></img>
				</div>
				<div className={cl.dateName}>
					<div className={cl.name}>
						{userName}
					</div>
					<div>
						{date}
					</div>
				</div>
			</div>
			<div className={cl.settings} ref ={rootEl}>
				<img src={require(`./settingIcon.png`)} alt='ава' 
					onClick={()=>{setSettingsModal(true)}} 
					onMouseOver={(e)=>{setSettingsModal(true)}} 
					></img>
			</div>
		</div>
	);
};

export default UpTools;