import React from 'react';
import cl from './Comments.module.scss'
import Comment from './Comment/Comment'
import axios from 'axios'
import { useState, useEffect } from 'react';

const Comments = ({users, commentsVisibility, postId,setCommentsLength, ...props}) => {

	const [comments, setComments] = useState([]);
	
	const getComments = (postId) => {
		axios.get(`https://jsonplaceholder.typicode.com/comments?postId=${postId}`).then(response =>
			{
				setComments(response.data)
				setCommentsLength(response.data.length)
			})
	}
	useEffect(() => {
		getComments(postId)
	}, []);

	const commentsClass = [cl.comments]
	
	if (commentsVisibility){
		commentsClass.push(cl.active)
	}
	return (
		<div className={commentsClass.join(' ')}>
			{comments.map((comment, step)=>
				<Comment key={comment.id} users={users} >{comment.body}</Comment>
			)}
		</div>
	);
};

export default Comments;