import React from 'react';
import cl from './DownTools.module.scss'
import { useState } from 'react'
import styled, { keyframes } from 'styled-components';
import { StyleSheet, css } from 'aphrodite';
import { tada, bounce, swing } from 'react-animations';
const DownTools = ({comments, setCommentsVisibility, commentsLength, ...props}) => {
	const [likes, setLikes] = useState(props.likes);
	const [dislikes, setDislikes] = useState(props.dislikes);
	const [likesClicked, setLikesClicked] = useState(false);

	const likesClass = [cl.like]
	const dislikesClass = [cl.dislike]
	if (likesClicked==='likes'){
		likesClass.push(cl.active)
	}
	if (likesClicked==='dislikes'){
		dislikesClass.push(cl.active)
	}

	const handleLikesClick = () => {
		if (likesClicked==='likes'){
			setLikes(prev=>prev-1)
			setLikesClicked(false)
			likesClass.pop(cl.active)
		}
		if (likesClicked==='dislikes'){
			setLikes(prev=>prev+1)
			setDislikes(prev=>prev-1)
			setLikesClicked('likes')
			dislikesClass.pop(cl.active)
			likesClass.push(cl.active)
		}
		if (!likesClicked){
			
			setLikes(prev=>prev+1)
			setLikesClicked('likes')
			likesClass.push(cl.active)
		}
	}

	const handleDislikesClick = () => {
		if (likesClicked==='dislikes'){
			dislikesClass.pop(cl.active)
			setDislikes(prev=>prev-1)
			setLikesClicked(false)
		}
		if (likesClicked==='likes'){
			dislikesClass.push(cl.active)
			likesClass.pop(cl.active)
			setDislikes(prev=>prev+1)
			setLikes(prev=>prev-1)
			setLikesClicked('dislikes')
		}
		if (!likesClicked){
			dislikesClass.push(cl.active)
			setDislikes(prev=>prev+1)
			setLikesClicked('dislikes')
		}
	}
	// const Bounce = styled.div`animation: 2s ${keyframes`${tada}`}`;
	// const Bounce2 = styled.div`animation: 2s ${keyframes`${tada}`}`;
	// <Bounce2></Bounce2>
	// <Bounce></Bounce>
	return (
		<div className={cl.body}>
			<div className={likesClass.join(' ')} onClick={()=>handleLikesClick()}>
				<img src={require('./icons/like.png')}></img>
				<div>{likes}</div>
			</div>
			<div className={dislikesClass.join(' ')} onClick={()=>handleDislikesClick()}>
				<img src={require('./icons/dislike.png')}></img>
				<div>{dislikes}</div>
			</div>
			
			<div className={cl.comments} onClick={()=>{setCommentsVisibility(prev=>!prev)}}>
				<img src={require('./icons/comments.png')}></img>
				<div>{commentsLength}</div>
			</div>
			

		</div>
	);
};

export default DownTools;