import React from 'react';
import cl from './PhotoModal.module.scss'
import { useState } from 'react' 


const PhotoModal = ({visible, step, setVisible, photos, photoStep, setPhotoStep, ...props}) => {
	const rootClasses  = [cl.modal]
	if (visible){
		rootClasses.push(cl.active)
	} 
	// onDoubleClick={()=>console.log('liked')}
	return (
		<div className={rootClasses.join(' ')} onClick={()=>{setVisible(false); setPhotoStep(0)}}>
			<div className={cl.content} onClick={(e)=>e.stopPropagation()}>
				<div className={cl.content__body}>
					<button className={cl.prev}
						onClick={()=>{setPhotoStep(prevState => prevState-1)}}></button>
					<img  src={photos[(photoStep>=0)?photoStep%photos.length: (photos.length-(-photoStep)%photos.length)%photos.length]}></img>
					<button className={cl.next}
						onClick={()=>{setPhotoStep(prevState => prevState+1)}}></button>
				</div>
			</div>
			
		</div>
	);
};

export default PhotoModal;